<template>
  <div class="page" style="padding-top: 50px;">
    <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    :title="title"
    :show-menu-button="true"
    :on-menu-button-click="toggleFilter"
    :on-back-button-click="back"
    :default-bg-color="true"
    :expanded="false"
    :animate="false"
    >
    <div slot="menu">
      <div class="filter-font">筛选</div>
    </div>
    </AnimateHeader>

    <div class="page-content user-account-detail">
      <!-- 筛选框 -->
      <div class="qk-slide-panel-wrapper" :class="{'active': filterState > 0}">
        <div v-if="filterState > 0" class="backdrop visible" :class="{'active': filterState == 2}"></div>

        <div class="qk-slide-panel" :class="{
          'visible': filterState == 1,
          'visible active': filterState == 2
        }">

          <ul v-for="buttons in filterButtons" class="filter-buttons">
            <li v-for="button in buttons">
              <div class="filter-button" :class="{active: filterType == button[0]}" @click="doFilter(button)">
                <span class="filter-text" v-text="button[1]"></span>
              </div>
            </li>
          </ul>

        </div>
        <div class="block-backdrop" @click="hideFilter()"></div>
      </div>

      <!-- 列表 -->
      <div class="lists">
        <scroller ref="listScroller" :onInfinite="infinite" noDataText="">
          <div v-if="loaded && details.length === 0" class="no-details">
            <img src="//assets.qkcdn.com/images/bf32cba5ef917a7e4c4721b4e495ee3c.png" alt="">
          </div>
          <div class="list-scroller-wrapper" v-for="(detail_data, index) in details_show" :key="index">
            <div class="year-date">{{index}}</div>
            <div class="detail-item" v-for="detail in detail_data">
              <div class="datetime">
                <div class="date">{{detail.date}}</div>
                <div class="time">{{detail.time}}</div>
              </div>
              <div class="icon">
                <img :src="detail.icon" :class="{'avatar': detail.is_avatar}">
              </div>
              <div class="amount" :class="{'minus': detail.sign == '-'}">
                <span class="sign">{{detail.sign}}</span>
                <span class="number">{{detail.amount}}</span>
                <span class="yuan">{{detail.unit}}</span>
              </div>
              <div class="title" v-html="detail.title" style="-webkit-box-orient: vertical;"></div>
              <div v-if="detail.desc" class="desc">原因：{{detail.desc}}</div>
              <div class="hairline-bottom"></div>
            </div>
          </div>
        </scroller>
      </div>

    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .filter-font {
    font-size: 14px;
    color: #3a3a3a;

    @include font-regular();
  }

  .user-account-detail {
    margin-top: 50px;
    padding-left: 20px;
    background-color: #fff;

    .qk-slide-panel-wrapper {
      position: fixed;
      left: 0;
      top: 44px;
      width: 100%;
      height: 100%;
      z-index: 7;
      display: none;

      &.active {
        display: block;
      }

      .backdrop {
        top: 44px;
        z-index: 0;
        background-color: rgba(0, 0, 0, 0.8);
      }
    }

    .qk-slide-panel {
      width: 100%;
      z-index: 1;
      background: #F5F5F5;
      padding-top: 30px;
      transition: transform 0.3s ease;
      -webkit-transition: -webkit-transform 0.3s ease;
      opacity: 0;

      &.visible {
        opacity: 1;
        transform: translate3d(0, -100%, 0);
        -webkit-transform: translate3d(0, -100%, 0);

        &.active {
          transform: translate3d(0, 0, 0);
          -webkit-transform: translate3d(0, 0, 0);
        }
      }

      .filter-buttons {
        display: flex;
        height: 50px;

        li {
          flex-grow: 1;
          margin-bottom: 3px;
          max-width: 33.3333%;

          .filter-button {
            position: relative;
            height: 34px;
            text-align: center;

            .filter-text {
              display: inline-block;
              line-height: 20px;
              font-size: 14px;
              color: #3A3A3A;

              @include font-regular();
            }

            &.active, &:active {
              .filter-text {
                color: #EF4F2A;
                border-bottom: 2px solid #EF4F2A;

                @include font-medium();
              }
            }
          }
        }
      }
    }

    .block-backdrop {
      position: relative;
      z-index: 1px;
      height: 100%;
    }

    .lists {
      .no-details {
        margin-top: 146px;
        width: 375px;
        height: 277px;

        img {
          width: 100%;
        }
      }

      .list-scroller-wrapper {
        margin-bottom: 20px;
        padding: 0 20px 0;

        .year-date {
          font-size: 20px;
          height: 28px;
          line-height: 28px;
          color: #DDDDDD;
          margin: 24px 0px 4px 3px;

          @include font-regular();
        }

        .detail-item {
          position: relative;
          min-height: 64px;
          background-color: $light;
          padding: 36px 16px 7px 116px;

          .hairline-bottom {
            @include hairline(bottom);

            margin-left: 16px;
          }

          .datetime {
            position: absolute;
            top: 14px;
            left: 0;
            width: 42px;
            text-align: right;
            color: #B5B5B5;

            @include font-regular();

            .date {
              height: 20px;
              line-height: 20px;
              font-size: 14px;
            }

            .time {
              height: 16px;
              line-height: 16px;
              font-size: 11px;
            }
          }

          .icon {
            position: absolute;
            top: 13px;
            left: 62px;
            width: 38px;
            height: 38px;
            line-height: 38px;

            img {
              width: 38px;
              height: 38px;
              border-radius: 3px;

              &.avatar {
                border-radius: 50%;
              }
            }
          }

          .amount {
            position: absolute;
            top: 8px;
            left: 120px;
            height: 25px;
            line-height: 25px;
            color: #3A3A3A;
            font-size: 10px;

            @include font-regular();

            .number {
              margin-left: -3px;
              font-size: 18px;
            }

            .yuan {
              margin-left: -3px;
            }
          }

          .amount.minus {
            color: #419BF9;
          }

          .title {
            margin-left: 3px;
            line-height: 18px;
            font-size: 13px;
            color: #3A3A3A;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            word-break: break-all;

            .error {
              color: $assertive;
            }
          }

          .hairline-bottom {
            visibility: hidden;
          }

          .desc {
            margin-top: 6px;
            line-height: 18px;
            font-size: 12px;
            color: #B5B5B5;
          }
        }
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'

  const filterButtons = [
    [[1, '全部'], [6, '试玩赚钱'], [8, '注册赚钱']],
    [[11, '办卡赚钱'], [4, '收徒赚钱'], [13, '淘宝返利']],
    [[9, '开店赚钱'], [10, '每日开奖'], [5, '提现']]
  ]

  export default {
    components: {
      AnimateHeader
    },

    data () {
      return {
        filterState: 0,
        filterType: 1,
        filterButtons: filterButtons,
        details: [],
        details_show: {},
        noMoreData: true,
        loaded: false,
        title: '账户明细'
      }
    },

    mounted () {
      this.refresh()
    },

    methods: {
      toggleFilter () {
        if (this.filterState === 0) {
          this.showFilter()
        } else if (this.filterState === 2) {
          this.hideFilter()
        }
      },

      showFilter () {
        this.filterState = 1
        setTimeout(() => {
          this.filterState = 2
        }, 60)
      },

      hideFilter () {
        this.filterState = 1
        setTimeout(() => {
          this.filterState = 0
        }, 300)
      },

      refresh () {
        const LIMIT = 10

        let params = {
          active: this.filterType,
          limit: LIMIT
        }

        this.$refs.listScroller.scrollTo(0, 0)

        this._getList(params, LIMIT)
      },

      doFilter (button) {
        this.filterType = button[0]
        this.noMoreData = true
        this.title = button[0] === 1 ? '账户明细' : button[1]
        this.details = []
        this.refresh()
        setTimeout(() => {
          this.hideFilter()
        }, 150)
      },

      infinite (done) {
        if (this.noMoreData) {
          done()
          return
        }

        const LIMIT = 10

        let params = {
          active: this.filterType,
          limit: LIMIT
        }

        let lastId = _.get(_.last(this.details), 'id', 0)
        if (lastId > 0) {
          params['last_id'] = lastId
        }

        this._getList(params, LIMIT)
      },

      _getList (params, LIMIT) {
        this.loaded = false

        return $api.accountDetail(params)
          .then(res => res.data && res.data.payload)
          .then((payload) => {
            _.forEach(payload, (detail) => {
              let m = moment(detail.datetime)
              detail.date = m.format('MM/DD')
              detail.time = m.format('HH:mm')

              if (!detail.integral) {
                detail.sign = '+'
                detail.amount = '0'
              } else if (/^[+-]/.test(detail.integral)) {
                detail.sign = detail.integral.substring(0, 1)
                detail.amount = detail.integral.substring(1)
              } else {
                detail.sign = '+'
                detail.amount = detail.integral
              }
            })

            setTimeout(() => {
              this.details = _.concat(this.details, payload)
              this.loaded = true
              this.details_show = _.groupBy(this.details, (detail) => {
                return moment(detail.datetime).format('YYYY年MM月')
              })
              payload.length < LIMIT ? this.noMoreData = true : this.noMoreData = false
              this.$refs.listScroller.finishInfinite(this.noMoreData)
            }, 300)
          })
      },

      back () {
        $router.back('/user')
      }
    }
  }
</script>



